import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Dialog from 'material-ui/Dialog';
import List, { ListItem, ListItemText } from 'material-ui/List';
import Divider from 'material-ui/Divider';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import Typography from 'material-ui/Typography';
import CloseIcon from 'material-ui-icons/Close';
import Slide from 'material-ui/transitions/Slide';

/* アイコンボタン */
import ActionAccountCircleIcon from 'material-ui-icons/AccountCircle';

/* メニュー */
import Menu, { MenuItem } from 'material-ui/Menu';

/*
 * アカウントダイアログ
 * 
 */
const styleSheet = createStyleSheet('AccountMenu', theme => ({
  root: {
    display: 'inline',
  },
  appBar: {
    position: 'relative',
	  backgroundColor:
	    theme.palette.type === 'light' ? theme.palette.primary[300] : theme.palette.accent[700]
  },
  flex: {
    flex: 1,
  },
}));

class AccountMenu extends Component {
  constructor(props) {
    super(props);
  }
  
  state = {
    menuAnchorEl: undefined,
    menuOpen: false,
    dialogOpen: false,
  };

  menuHandleClick = event => {
    this.setState({ menuOpen: true, menuAnchorEl: event.currentTarget });
  };

  menuHandleRequestClose = () => {
    this.setState({ menuOpen: false });
  };

  dialogHandleRequestClose = () => {
    this.setState({ dialogOpen: false });
  };

  dialogHandleOpen = () => {
    this.setState({ menuOpen: false });
    this.setState({ dialogOpen: true });
  };

  /*
   * アカウントアイコン
   * 親コンポーネントでアイコンが指定されていたら表示する
   *
   */
  iconUpdate() {
    return this.props.icon ? this.props.icon : <ActionAccountCircleIcon />;
  };

  /*
   * テーマ変更リスト
   * 親コンポーネントでコールバックが指定されていたらテーマ変更リストを表示する
   *
   */
	renderThemeList() {
		if(this.props.onThemeChange){
	    return <ListItem button>
	             <ListItemText primary="テーマの変更" secondary={"light"} onClick={this.props.onThemeChange} />
	           </ListItem>;
	  }
	};

  render() {
    const classes = this.props.classes;
    return (
      <div className={classes.root}>

				{/* アイコン */}
        <IconButton color="default" aria-owns="simple-menu" aria-haspopup="true" onClick={this.menuHandleClick}>
        	{this.iconUpdate()}
        </IconButton>

				{/* メニュー */}
        <Menu
          id="simple-menu"
          anchorEl={this.state.menuAnchorEl}
          open={this.state.menuOpen}
          onRequestClose={this.menuHandleRequestClose}
        >
          <MenuItem onClick={this.dialogHandleOpen}>アカウント情報</MenuItem>
          <MenuItem onClick={this.menuHandleRequestClose}>ログアウト</MenuItem>
        </Menu>

				{/* ダイアログ */}
        <Dialog
          fullScreen
          open={this.state.dialogOpen}
          onRequestClose={this.dialogHandleRequestClose}
          transition={<Slide direction="up" />}
        >
          <AppBar color="primary" className={classes.appBar}>
            <Toolbar>
              <IconButton color="default" onClick={this.dialogHandleRequestClose} aria-label="Close">
                <CloseIcon />
              </IconButton>
              <Typography type="title" color="default" className={classes.flex}>
                アカウント情報
              </Typography>
            </Toolbar>
          </AppBar>
          <List>
            <ListItem button>
              <ListItemText primary="ログインアカウントの編集" secondary="担当太郎"/>
            </ListItem>
            <Divider />
            <ListItem button>
              <ListItemText primary="企業情報の編集" secondary="人材派遣株式会社" />
            </ListItem>
            <Divider />
            {this.renderThemeList()}
          </List>
        </Dialog>
      </div>
    );
  }
}

AccountMenu.propTypes = {
  classes: PropTypes.object.isRequired,
  icon: PropTypes.object,
  onThemeChange: PropTypes.func,
};

export default withStyles(styleSheet)(AccountMenu);



