import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Chip from 'material-ui/Chip';
import TextField from 'material-ui/TextField';

import Dialog, { DialogContent } from 'material-ui/Dialog';


const styleSheet = createStyleSheet('ChipsArray', theme => ({
  chip: {
    margin: theme.spacing.unit / 2,
    color: theme.palette.type === 'light' ? 'white' : theme.palette.accent[900],
    backgroundColor: theme.palette.primary[400],
  },
  row: {
    display: 'flex',
    justifyContent: 'left',
    flexWrap: 'wrap',
    padding: '20px 0px 20px 10px',
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  input: {
    fontSize: '0.8em',
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
}));

	/*
	 * チップクラス
	 *
	 */
class ChipsArray extends Component {
  state = {
    index: 0,  // とりあえず0で初期化
    open: false,
  };

  /*
   * ダイアログ展開状態
   */
  handleRequestClose = () => {
    this.setState({ open: false });
  };

	/*
	 * テキストフィールド編集時のイベント
	 * 編集したテキストをステートにセットする
	 *
	 * index: 編集対象の配列番号
	 * event: 編集したイベントデータ
	 *
	 */
  handleRequestChange = index => event => {
    const chipData = JSON.parse(JSON.stringify(this.props.chipData));	// 参照ではなくコピー
    chipData[index].value = event.target.value;

		this.props.onChange(chipData);	// 変更結果をコールバックで親に渡す
  };

	/*
	 * チップクリック時のイベント
	 * ダイアログを表示する
	 *
	 * index: クリック対象のイベントデータ
	 *
	 */
  handleRequestClick = index => () => {
    // チップ編集インデックスを保存
    this.setState({ index: index });

    // ダイアログオープン
    this.setState({ open: true });
  };

	/*
	 * チップ削除時のイベント
	 *
	 * data: 削除したイベントデータ
	 *
	 */
  handleRequestDelete = index => () => {
    const chipData = JSON.parse(JSON.stringify(this.props.chipData));	// 参照ではなくコピー
    chipData[index].value = '';

		this.props.onChange(chipData);	// 変更結果をコールバックで親に渡す
  };

  render() {
    const classes = this.props.classes;

		{/* チップ追加 */}
    const chipData = JSON.parse(JSON.stringify(this.props.chipData));	// 参照ではなくコピー

		{/* チップ編集インデックス */}
    const index = this.state.index;

    return (
      <div className={classes.row}>

        {chipData.map(data => {
          if(data.value){
            {/* テキストが存在すれば表示する */}
	          return (
	            <Chip
	              label={data.value + '（' + data.label + '）'}
	              key={data.key}
                onClick={this.handleRequestClick(data.key)}
	              onRequestDelete={this.handleRequestDelete(data.key)}
	              className={classes.chip}
	            />
	          );
          }
        })}

				{/* ダイアログ */}
	      <div className={classes.container}>
	        <Dialog open={this.state.open} onRequestClose={this.handleRequestClose}>
	          <DialogContent>
			        <TextField
			          autoFocus={true}
			          id={chipData[index].id}
			          label={chipData[index].label}
			          type={chipData[index].type}
			          className={classes.input}
                helperText="検索結果：n 件"
			          value={chipData[index].value}
			          onChange={this.handleRequestChange(index)}
			          InputProps={{ placeholder: chipData[index].placeholder }}
			          marginForm
			        />
	          </DialogContent>
	        </Dialog>
	      </div>

      </div>
    );
  }
}

/*
 * [props]プロパティ
 *
 *   chipData: [{ key: 0, id: 'id', type: 'text', label: 'ラベル', value: 'バリュー', placeholder: '例）あいうえお' }];
 *   ※必須、配列で指定、keyはユニークであること
 *
 *   onChange: function();
 *   ※必須、チップ変更時のコールバック関数を指定
 *
 */
ChipsArray.propTypes = {
  classes: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
	chipData: PropTypes.arrayOf(	// 配列かつ
		PropTypes.shape({						// 以下の型かどうか
		  key:        PropTypes.number.isRequired,
		  id:         PropTypes.string,
		  type:       PropTypes.string,
		  label:      PropTypes.string,
		  value:      PropTypes.string,
		  placeholder:PropTypes.string,
		})
	).isRequired,
};

export default withStyles(styleSheet)(ChipsArray);




