import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Menu, { MenuItem } from 'material-ui/Menu';

/* アイコン */
import IconButton from 'material-ui/IconButton';
import FavoriteIcon from 'material-ui-icons/Favorite';

const styleSheet = createStyleSheet('SelectedMenu', theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    display: 'inline',
  },
}));

class SelectedMenu extends Component {
  state = {
    anchorEl: undefined,
    open: false,
    selectedIndex: 0,  // デフォルト選択番号
  };

  /*
   * React component ライフサイクル
   *
   * componentWillReceiveProps()
   * Propが更新される時に呼ばれます。ちなみにComponentが新しく
   * DOMツリーに追加される時には呼ばれません。親ComponentのStateが
   * Propとして渡されていて、その値が変化した時に画面の表示以外で
   * 何かしたいときに使う感じです。
   * 後はPropの値に応じてStateの値を更新したいようなときに使います。
   *
   */
  componentWillReceiveProps(nextProps) {
    // 親からのprops更新によってアクションを起こす
    if(typeof nextProps.index !== 'undefined'){
      this.setState({ selectedIndex: nextProps.index });
    }
  };

  handleClickListItem = event => {
    this.setState({ open: true, anchorEl: event.currentTarget });
  };

  handleMenuItemClick = (index, key) => {
    this.setState({ selectedIndex: index, open: false });

    if(this.state.selectedIndex !== index){
	    // メニューが変更され、かつ、コールバックが指定されていれば呼び出す
	    if(this.props.onMenuChange){
	      this.props.onMenuChange(index, key);
	    }
    }
  };

  handleRequestClose = () => {
    this.setState({ open: false });
  };

  /*
   * アカウントアイコン
   * 親コンポーネントでアイコンが指定されていたら表示する
   *
   */
  iconUpdate() {
    return this.props.icon ? this.props.icon : <FavoriteIcon />;
  };

  render() {

    const classes = this.props.classes;
    const listData = [...this.props.listData];

    return (
      <div className={classes.root}>

        {/* アイコン */}
	      <IconButton
	        aria-owns="list-menu"
	        aria-haspopup="true"
	        aria-controls="favorite-menu"
	        onClick={this.handleClickListItem}
	      >
	      	{this.iconUpdate()}
	      </IconButton>

	      <Menu
	        id="favorite-menu"
	        anchorEl={this.state.anchorEl}
	        open={this.state.open}
	        onRequestClose={this.handleRequestClose}
	      >
	        {listData.map((data, index) =>
	          <MenuItem
	            key={data.key}
	            selected={index === this.state.selectedIndex}
	            onClick={event => this.handleMenuItemClick(index, data.key)}
	          >
	            {data.value}
	          </MenuItem>,
	        )}
	      </Menu>
	    </div>
    );
  }
}

SelectedMenu.propTypes = {
  classes: PropTypes.object.isRequired,
  listData: PropTypes.array.isRequired,
  icon: PropTypes.object,
  index: PropTypes.number,
  onMenuChange: PropTypes.func,
};

export default withStyles(styleSheet)(SelectedMenu);



