import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import TextField from 'material-ui/TextField';

const styleSheet = createStyleSheet('TextFields', theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0px 0px 20px 20px',
  },
  input: {
    fontSize: '0.8em',
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 180,
  },
}));

	/*
	 * テキストフィールドクラス
	 *
	 */
class TextFields extends Component {

	/*
	 * テキストフィールド編集時のイベント
	 * 編集したテキストをステートにセットする
	 *
	 * index: textDataの編集対象の配列番号
	 * event: 編集したイベントデータ
	 *
	 */
  handleRequestChange = index => event => {
    const textData = JSON.parse(JSON.stringify(this.props.textData));	// 参照ではなくコピー
    textData[index].value = event.target.value;

		this.props.onChange(textData);	// 変更結果をコールバックで親に渡す
  };

  render() {
    const classes = this.props.classes;
    const textData = JSON.parse(JSON.stringify(this.props.textData));	// 参照ではなくコピー

    return (
      <div className={classes.container}>

        {textData.map(data => {
          return (
		        <TextField
		          key={data.key}
		          id={data.id}
		          label={data.label}
		          type={data.type}
		          className={classes.input}
		          value={data.value}
		          onChange={this.handleRequestChange(data.key)}
		          InputProps={{ placeholder: data.placeholder }}
		          marginForm
		        />
          );
        })}

      </div>
    );
  }
}

/*
 * [props]プロパティ
 *
 *   textData: [{ key: 0, id: 'id', type: 'text', label: 'ラベル', value: 'バリュー', placeholder: '例）あいうえお' }];
 *   ※必須、配列で指定、keyはユニークであること
 *
 *   onChange: function();
 *   ※必須、テキスト変更時のコールバック関数を指定
 *
 */
TextFields.propTypes = {
  classes: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
	textData: PropTypes.arrayOf(	// 配列かつ
		PropTypes.shape({						// 以下の型かどうか
		  key:        PropTypes.number.isRequired,
		  id:         PropTypes.string,
		  type:       PropTypes.string,
		  label:      PropTypes.string,
		  value:      PropTypes.string,
		  placeholder:PropTypes.string,
		})
	).isRequired,
};

export default withStyles(styleSheet)(TextFields);




