import React from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import Typography from 'material-ui/Typography';

/* アカウントダイアログ */
import AccountMenu from './AccountMenu';

/* ドロワー */
import UndockedDrawer from './Drawer';

const styleSheet = createStyleSheet('ButtonAppBar', {
  root: {
    width: '100%',
  },
  flex: {
    flex: 1,
  },
});

/*
 * Functional Component
 *
 * Reactのコンポーネントは、このFunctional Component（Stateless Functional Component / SFC）の
 * スタイルで書くのが基本です。JavaScriptの関数で書くコンポーネントです。JSXと呼ばれる構文を用
 * いてUIを構築し、関数の戻り値として返します。内部で状態は持たず、コンポーネントで使用したい値
 * はpropsとして引数を通して受け取ります。関数名の頭文字は大文字となります。ES6のアローファンク
 * ション構文で書くとかなりシンプルに書くことができます。受け取ったpropsに応じたViewを返すだけの
 * ものなので、テストも楽というのが特徴です。
 *
 */
function ButtonAppBar(props) {

  const classes = props.classes;
  return (
    <div className={classes.root}>
      <AppBar position="fixed" color="default" >
        <Toolbar>
       		<UndockedDrawer />

       		{/* Typography color = "inherit","secondary","accent","default" */}
          <Typography type="title" color="secondary" className={classes.flex}>
            Navi analyzer
          </Typography>

          <AccountMenu onThemeChange={props.onThemeChange} />

        </Toolbar>
      </AppBar>
    </div>
  );
}

ButtonAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
  onThemeChange: PropTypes.func,
};

export default withStyles(styleSheet)(ButtonAppBar);



