import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

/*
 * Material-UI
 * 
 * v0.15.0から、Material-UIコンポーネントはテーマを提供する必要があります。
 * 起動して実行する最も簡単な方法はMuiThemeProvider、アプリケーションコンテキストにテーマを挿入することです。
 * それに続いて、私たちのドキュメントに示されているようなコンポーネントを使用することができます。
 */
import { MuiThemeProvider, createMuiTheme } from 'material-ui/styles';
import createPalette from 'material-ui/styles/palette';
import lime from 'material-ui/colors/lime';
import red from 'material-ui/colors/red';
import cyan from 'material-ui/colors/cyan';
import blueGrey from 'material-ui/colors/blueGrey';

/*
 * User Component
 *
 * ユーザーコンポーネントの読み込み
 */
import StaffList from './stafflist'
import AppBar from './AppBar'


class App extends React.Component {

  state = {
    themeType: 'light',  // デフォルト=ライトテーマ
  };

  /*
   * テーマ変更コールバック
   *
   */
  callBackThemeChange = () => {
    this.state.themeType === 'light' ?
      this.setState({themeType: 'dark'}) : this.setState({themeType: 'light'});
  };

  render() {
		/*
		 * テーマ設定
		 * 
		 */
		const theme = createMuiTheme({
		  palette: createPalette({
		    type: this.state.themeType,
		    primary: this.state.themeType === 'light' ? cyan : lime,
		    accent: this.state.themeType === 'light' ? lime : blueGrey,
		    error: red,
		  }),
		});

		/*
		 * BackSeet のスタイル
		 * 
		 */
		const styles = {
		  backseat: {
 		  	backgroundColor: this.state.themeType === 'light' ?
		      theme.palette.primary.A400 : theme.palette.accent[700],
		  	height: '260px',
		  },
		  contentbox: {
		    position: 'relative',
		    top: '-220px',
		    padding: '20px',
		  },
		};

    return (
	    <MuiThemeProvider theme={theme}>

	      <div>
	        <AppBar onThemeChange={this.callBackThemeChange} />
	        <div style={styles.backseat} />
		      <div style={styles.contentbox}>
		        <StaffList />
		      </div>
	      </div>

	    </MuiThemeProvider>
    );
  }
}

export default App;



