import React, { Component } from 'react';
import Snackbar from 'material-ui/Snackbar';
import PropTypes from 'prop-types';

class PositionedSnackbar extends Component {
  state = {
    open: false,
    vertical: 'top',
    horizontal: 'right',
    autoHide: 3000,
    message: null,
  };

  handleRequestClose = () => {
    this.setState({ open: false });
    this.setState({ message: null });
    if(this.props.onClose){
      this.props.onClose();	// 親のクローズコールバック
    }
  };

  /*
   * messageに文字列が存在すればオープン
   */
  openMessage(message) {
    if(typeof message !== 'undefined' && message !== null && message !== ''){
      this.setState({ message: message });
	    this.setState({ open: true });
	  }
  }

  /*
   * React component ライフサイクル
   *
   * componentWillMount()
   * ComponentがDOMツリーに追加される前に一度だけ呼ばれます。
   * なので初期化処理を行うのに適しています。
   * この中でsetStateするとrender時にまとめて行われます。
   * server-side rendering時にも呼ばれるのでどちらでも動くコ
   * ードである必要があります。
   *
   */
  componentWillMount() {
    this.openMessage(this.props.message);	// 初回のDOM初期化時にも文字列を表示する場合を考慮
  };

  /*
   * React component ライフサイクル
   *
   * componentWillReceiveProps()
   * Propが更新される時に呼ばれます。ちなみにComponentが新しく
   * DOMツリーに追加される時には呼ばれません。親ComponentのStateが
   * Propとして渡されていて、その値が変化した時に画面の表示以外で
   * 何かしたいときに使う感じです。
   * 後はPropの値に応じてStateの値を更新したいようなときに使います。
   *
   */
  componentWillReceiveProps(nextProps) {
    // 親からのprops更新によってアクションを起こす
    if(typeof nextProps.vertical !== 'undefined'){
      this.setState({ vertical: nextProps.vertical });
    }
    if(typeof nextProps.horizontal !== 'undefined'){
      this.setState({ horizontal: nextProps.horizontal });
    }
    if(typeof nextProps.autoHide !== 'undefined'){
      this.setState({ autoHide: nextProps.autoHide });
    }
    this.openMessage(nextProps.message);
  };

  render() {
    const { open, vertical, horizontal, autoHide, message } = this.state;
    return (
      <div>
        <Snackbar
          anchorOrigin={{ vertical, horizontal }}
          open={open}
          onRequestClose={this.handleRequestClose}
          SnackbarContentProps={{
            'aria-describedby': 'message-id',
          }}
          autoHideDuration={autoHide}
          message={<span id="message-id">{message}</span>}
        />
      </div>
    );
  }
}

/*
 * プロパティ
 *
 * message:    表示文字列(空文字列の場合はスナックバーを表示しない)
 * onClose:    クローズ時コールバック
 * vertical:   縦位置  'top', 'bottom'
 * horizontal: 横位置  'left', 'center', 'right'
 * autoHide:   表示時間  null=継続表示, (数値)=msec
 */
PositionedSnackbar.propTypes = {
  message: PropTypes.string,
  onClose: PropTypes.func.isRequired,
  vertical: PropTypes.string,
  horizontal: PropTypes.string,
  autoHide: PropTypes.number,
};

export default PositionedSnackbar;




